@import 'ScssHelpers/helpers-all.scss';

.team_slider__slide_link {
	display: block;
	padding-left: 6.3rem;

	color: $black;

	&:hover,
	&:focus {
		text-decoration: none;
	}
}

.team_slider__slide_title {
	position: absolute;
	bottom: 7rem;
	left: -3px;
	display: inline-flex;

	writing-mode: vertical-lr;
	transform: rotate(-180deg);
	line-height: 1;

	text-transform: uppercase;
	font-weight: 400;
	z-index: 2;

	@include bp($point_2, min) {
		font-size: 6rem;
	}
	@include bp($point_3) {
		font-size: 5.2rem;
	}
}

.team_slider__img_w {
	position: relative;
	display: block;
	width: 100%;
	height: 100%;
	overflow: hidden;

	@include bp($point_2, min) {
		padding-bottom: 132%;
	}
	@include bp($point_3) {
		padding-bottom: 130%;
	}
}

.team_slider__img_in {
	object-fit: cover;
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
}

.team_slider__slide_content {
	padding: 1.8rem 0 1rem;

	font-size: 1.6rem;
	font-family: $raleway;
	font-weight: bold;
	color: $black_3;
	text-transform: uppercase;

	border-bottom: 4px solid $black;

	@include bp($point_2, min) {
		line-height: 1.15;
	}
	@include bp($point_3) {
		line-height: 1.2;
	}
}
