@import 'ScssHelpers/helpers-all.scss';

.team_slider__wrapper {
	@include bp($point_2, min) {
		padding-top: 13.1rem;
		padding-bottom: 9.1rem;
	}

	@include bp($point_3) {
		padding-top: 8rem;
	}
}

.team_slider__slider {
	overflow: visible;
	
	@include bp($point_2, min) {
		margin-right: calc((100vw - $page_width) / 2 * -1);
	}
	@include bp($point_3) {
		margin-right: calc(var(--gl-indent) * -1);
	}
}

.team_slider__title_main {
	text-decoration: underline;
	text-transform: uppercase;
	font-weight: 400;
	color: #222;
	line-height: 1;

	@include bp($point_2, min) {
		margin-bottom: 5rem;

		font-size: 9rem;
	}
	@include bp($point_3) {
		margin-bottom: 4rem;

		font-size: 5.2rem;
	}
}

.team_slider__slide {
	@include bp($point_2, min) {
		width: 35.8rem;
		padding-right: 5rem;
	}

	@include bp($point_3) {
		width: 29.8rem;
		padding-right: 3.2rem;
	}
}
